@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.container {
  width: 1000px;
  padding: 10px;
  margin: 0 auto;
}

.products {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.store {
  display: flex;
  flex-direction: column;
}

.icon-switch {
  align-self: flex-end;
  font-size: 2em;
  cursor: pointer;
}

.shop-card {
  display: flex;
  flex-wrap: wrap;
  align-content: space-between;
  width: 320px;
  height: 320px;
  background-repeat: no-repeat;
  background-size: contain;
  margin-bottom: 10px;
  padding: 30px;
}

.shop-card__name,
.shop-item__name {
  width: 100%;
  flex-basis: 100%;
  align-self: center;
  text-align: center;
  text-transform: uppercase;
  font-size: 1.5em;
  font-weight: bold;
}

.shop-item__name {
  width: auto;
  flex-basis: 20%;
  text-align: center;
}

.shop-card__color {
  width: 100%;
  height: 80%;
  flex-basis: 100%;
  text-align: center;
}

.shop-item__color{
  flex-basis: 20%;
  text-align: center;
}

.shop-card__price,
.shop-item__price {
  color: coral;
  flex-basis: 50%;
  align-self: center;
  font-size: 1.2em;
}

.shop-item__price {
  flex-basis: 20%;
  text-align: center;
}

.shop-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 80px;
  width: 100%;
  background-color: #F5F5F5;
  padding: 0 20px;
  margin-bottom: 5px;
}

.shop-item_image {
  height: 100%;
}

.add-to-cart-button {
  color: coral;
  flex-basis: 50%;
  text-transform: uppercase;
  font-weight: bold;
  padding: 5px 10px;
  border: 1px solid coral;
  border-radius: 20px;
}

.shop-item > .add-to-cart-button {
  flex-basis: 20%;
}

